
@media only screen and (min-width: 750px) {
.nav{
    width:100%;
    height:50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

   color:white;
   background-color: rgb(39, 39, 42);
   border-bottom: 0.5px solid rgb(228, 199, 199);
}
}


.icons-header-warper{
    display: flex;
    flex-direction: row;
}
.warper{
        display: flex;
        flex-direction: row;
        margin-right: 40px; 
}
.icons_profile_warper{
    display: flex;
    flex-direction: row;
    margin-right: 40px; 
}

.side_nav_warper{
    padding-top: 10px;
    height: 100vh;
    max-width: 200px;
    background-color:rgb(231, 1, 1);
    position: fixed;
    top: 0;
    right:0;
}
.logo_warper{
    width: 200px;
    position: absolute;
    bottom:0;

}
.side_nav_warper{
    color: white;
    width: 100%;
}

.nav_link{
    color: white;
    text-decoration: none;
    font-size: 18px;
    padding-left: 20px;
    padding-right: 20px;
}
.link_warper{
    align-items: right;
    padding: 15px;
    border-bottom: 1px solid  white;
}

@media only screen and (max-width: 750px) {
    .icons_profile_warper{
       margin-right: none !important;
      
    }
    .nav{
        justify-content: center;
    }
}

@media only screen and (max-width: 400px) {
   #name_profile_avatar{
       width:120px;
   }
   .nav{
    background-color: rgb(39, 39, 42);
     width:100%
   }
}
@media only screen and (max-width: 900px) {
    
    .nav{
        color:white;
        width:100%;
        background-color: rgb(39, 39, 42);
    }
 }

 @media only screen and (max-width: 750px) {
    .name{
        position: relative;
        right:95%;
    }
    .icons-header-warper{
     /* padding-left: 100px; */
     background-color: rgb(39, 39, 42);
    }
    .warper{
     padding-left: 170px;
    }
    .menu-bt{
        position: relative;
        top:50%;
        right: 30px;
    }
 }
 @media only screen and (max-width: 650px) {
    .name{
       
    }
    .icons-header-warper{
     margin-left: none;
    }
    
 }

 @media only screen and (max-width: 450px) {
 
    .logo_warper{
        width: 200px;
        height: 100px !important;
        position: absolute;
        bottom:0;
    
    }
    .nav{
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: none;
    }
    .name{
        flex-direction: column;
    }
 }
